<template>
  <div style="background: #eee;">
  <section class="sec5-2 section">
    <span class="span">как устроена</span>
    <h2>Партнерская программа</h2>
    <div class="desc">
      <div class="item">
        <div class="list" v-for="(s,i) in step" :key="i"><span class="gold">{{i+1}}</span> {{s}}</div>
      </div>
      <div class="item">
        <img :src="require('../assets/img/roadmap.png')" />
      </div>
    </div>
  </section>
</div>
</template>

<script>
export default {
  data(){
    return {
      step: [
        `После регистрации в системе вам открывается доступ к Личному кабинету партнера`,
        `Вы получаете готовые рекламные материалы для анонса наших обучающих программ`,
        `Размещаете тексты и баннеры в соц.сетях, делаете рассылку по своей подписной базе`,
        `По вашей партнерской ссылке регистрируются потенциальные клиенты (каждый клиент закрепляется за вами)`,
        `Мы продаем с вебинаров, через емайл-маркетинг и силами собственного отдела продаж — вы зарабатываете с каждой покупки привлеченных клиентов!`,
        `С нашей стороны с вами работает отдельный менеджер по партнерской программе`,
      ]
    }
  }
}
</script>
<style lang="stylus" scoped>
h2
  font-size 5vw
.sec5-2
  font-family 'GothamProBold'
  padding-bottom 40px
  padding-top 40px
  background #EDEFEE
  .desc
    display flex
    align-items center
    width 90%
    margin auto
    margin-top 40px
    .item
      width 50%
      img
        width 80%
        margin 10%
  .list
    font-family "Open Sans"
    display flex
    width 80%
    margin-top 10px
    margin-bottom 10px
    &:first-child
      .gold
        margin-left 10px
        left -5px
    .gold
      font-family 'GothamProBold'
      font-size 3em
      position relative
      display block
      color #C0A585
      margin-right 10px
  h2
  .span
    display block
    text-align center

@media (max-width: 780px)
  .sec5-2
    .desc
      flex-direction column
      .item
        width 100%

</style>



