<template>
  <div style="background: #eee">
  <section style="max-width: 1200" class="sec7 section">
    <h6>Продукты, цены и комиссионные</h6>
    <h2>сколько можно заработать</h2>
    <p>Все зависит от того, сколько клиентов придет по вашей рекомендации. В среднем активные партнеры зарабатывают 30-50 тысяч рублей за запуск. Если у вас большая база, вы можете заработать и 100, и 150 тысяч, и больше. </p>
    <div class="courselist">
      <div class="label" v-for="(d,i) in data" :key="i">
        <div class="head">
          <span class="type">
            {{d.type}}
          </span>
          <span class="name">
            {{d.name}}
            <br v-if="d.isOpen" />
            <small v-if="d.isOpen">{{d.comment}}</small>
          </span>
          <img v-if="!d.isOpen" :src="require('../assets/img/add.png')">
          <img v-else :src="require('../assets/img/close.png')">
        </div>
        <div class="body" v-if="d.isOpen">
          <div class="row-h">
            <span>тариф</span>
            <span>цена руб*</span>
            <span>Партнёрский процент</span>
            <span>Ваши комиссионные, руб.</span>
          </div>
          <div v-for="(D, k) in d.steps" :key="k" class="row-b">
            <span>{{D.name}}</span>
            <span>{{D.p}}</span>
            <span>{{D.s}}</span>
            <span>{{D.f}}</span>
          </div>
          <small>*Указана базовая цена тарифов. В разные окна продаж возможно уменьшение базовой цены. Актуальная цена всегда указана на сайте курса.</small>
        </div>
      </div>
    </div>
    <div class="info">
      <h3>Важно!</h3>
      <div class="item">
        <div class="list" v-for="(s,i) in step" :key="i"><span class="gold">{{i+1}}</span> {{s}}</div>
      </div>
    </div>
  </section>
</div>
</template>

<script>
export default {
  data(){
    return {
      step: [
        `Партнерское вознаграждение начисляется после вычета комиссии платежной системы, которая составляет 3-5% в зависимости от способа оплаты, выбранного клиентом.` ,
        `Партнерское вознаграждение выплачивается с 1 по 10 число месяца за предыдущий месяц.`,
        `Партнерское вознаграждение рассчитывается как указанный процент за заказ и 2% за последующие заказы приведенного реферала.`
      ],
      data: [
        {
          name: "\"Финансовая перезагрузка\"",
          type: "онлайн-курс",
          comment: "Сайт курса: www.fin-1.com/finrestart",
          isOpen: true,
          steps: [
            {name: "Финансовая безопасность", p:'19 970',s: '25%', f: '4 992'},
            {name: "Финансовая стабильность", p:'34 970',s: '20%', f: '6 994'},
            {name: "Финансовая независимость", p:'59 970',s: '20%', f: '11 994'},
            {name: "Финансовая свобода", p:'199 370',s: '15%', f: '29 995'}
          ]
        },
        {
          name: `"Клуб Миллионеров"`,
          type:"тренинг",
          isOpen: true,
          steps: [
            {name: "КМ ст", p:'71 000',s: '10%', f: '7 100'},
            {name: "КМ бизнес", p:'109 000',s: '10%', f: '10 900'},
            {name: "КМ ВИП", p:'199 000',s: '7%', f: '13 930'},
            {name: "КМ премиум", p:'499 000',s: '5%', f: '24 950'}
          ]
        },
        {
          name: `"Сверхвозможности" в Москве`,
          type:"тренинг бесстрашия",
          isOpen: true,
          steps: [
            {name: "Сверхв-ти", p:'28 000',s: '10%', f: '2 800'},
          ]
        },
        {
          name: `"Золотой запас"`,
          type:"видеокурс в записи",
          isOpen: true,
          steps: [
            {name: "ЛФС стандарт", p:'2 990',s: '10%', f: '897'},
            {name: "ЛФС ВИП", p:'5 990',s: '10%', f: '4 193'},
          ]
        },
        {
          name: `"Личная финансовая стратегия"`,
          type:"интенсив",
          isOpen: true,
          steps: [
            {name: "Золотой запас", p:'499',s: '30%', f: '149'},
          ]
        },
      ]
    }
  }
}
</script>
<style lang="stylus" scoped>
h2
  font-size 5vw
.row-h
  font-size .8em
.sec7
  background #EDEFEE
  padding-top 40px
  padding-bottom 40px
  .info
    display flex
    align-items center
    margin-top 40px
    h3
      color #C0A585
      font-size 2em
      font-family 'GothamProBold'
      width 50%
      text-align right
      padding-right 40px
  .list
    font-family "Open Sans"
    display flex
    width 80%
    margin-top 10px
    margin-bottom 10px
    &:first-child
      .gold
        margin-left 10px
        left -5px
    .gold
      font-family 'GothamProBold'
      font-size 3em
      position relative
      display block
      color #C0A585
      margin-right 10px
  .courselist
    width 80%
    margin auto
    margin-top 40px
  .label
    .row-h
      display flex
      color gray
      white-space nowrap
      span
        width 20%
        line-height 2em
        &:first-child
          width 35%
    .body
      small
        color gray
        padding-bottom 20px
        padding-top 20px
        display block
    .row-b
      display flex
      border-top 1px solid lightgray
      span
        width 20%
        font-size 1.2em
        white-space nowrap
        line-height 3em
        &:first-child
          width 35%
    .head
      display flex
      align-items center
      justify-content space-between
      .type
        color rgba(0,0,0,.6)
      .name
        font-family 'GothamProBold'
        font-size 1.6em
        line-height 3em
        small
          font-size .5em
      img
        width 30px
        cursor pointer
    border-top 2px solid #BC9F7D
    &:last-child
      border-bottom 2px solid #BC9F7D
  h2
  h6
    font-family 'GothamProBold'
    text-align center
  h6
    color #333333
  p
    width 80%
    margin auto
    text-align center
    max-width 420px
@media (max-width: 760px)
  .sec7
    .list
      margin-left 10%
    .info
      flex-direction column
      h3
        text-align center
    .label
      .head
        .name
          font-size 1em
      .body
        display none
</style>



