<template>
  <div style="width: 100%; background: #eee">
  <section class="sec6 section">
      <div class="desc-d">Рекламируя программы Максима Темченко</div>
      <h2>вы рекомендуете <br>качественное обучение <br>от профессионала</h2>
      <div class="main">
          <div class="item">
              <div v-for="(w,i) in what" :key="i" class="box-item-user">
                  <div class="box-arrow" id="ba1">
                      <div style="font-size: .8em" class="text">{{w.q}}</div>
                  </div>
                  <div class="box-text" style="padding-top: 10px">{{w.w}}</div>
              </div>
          </div>
          <div class="item">
              <img :src="require('../assets/img/12.png')">
          </div>
      </div>
  </section>
</div>
</template>

<script>
export default {
  data(){
    return {
      what: [
        {q: 1750,w: "человек прошли курс «Финансовая перезагрузка» за 1 год"},
        {q: '32345',w: "человек прошли «Клуб Миллионеров» за 8 лет"},
        {q: 879,w: "человек побывали на выездном тренинге «Матрица-Перезагрузка» за 5 лет"}
      ]
    }
  }
}
</script>
<style lang="stylus" scoped>
h2
  font-size 5vw
.sec6
  padding-top 40px
  padding-bottom 40px
  .main
    width 100%
    display flex
    align-items center
    margin-top 40px
    .item
      width 50%
      & > img
        width 50%
        margin-left 25%
  .box-item-user
    display flex
    margin-top 20px
    margin-bottom 20px
    .box-arrow
      width 140px
      flex-shrink 0
      position relative
      top 20px
  .desc-d
    font-family 'GothamProBold'
    text-align center
  h2
    font-family 'GothamProBold'
    text-align center

@media (max-width: 760px)
  .sec6
    .main
      display flex
      flex-direction column-reverse
      .item
        width 100%
        & > img
          margin-top 40px
          width 50%
          margin-left 25%

</style>



