<template>
  <section class="sec8 section">
    <h2>рекламные материалы</h2>
    <p>У нас есть готовые базовые рекламные материалы на наши флагманские продукты: письма для рассылок, баннеры и посты для соцсетей. В каждое письмо и пост ам нужно вставлять свою партнерскую ссылку перед оправкой/публикацией и отредактировать актуальную информацию <span style="margin-right:0.3em;"> </span><span style="margin-left:-0.3em;">(</span>даты, время, актуальные цены).<br />
 <br/>Чтобы не попасть в СПАМ, рекомендуем тему и текст писем изменять под свой стиль и «затачивать» под свою аудиторию.  А партнерские ссылки<span style="margin-right:0.44em;"> </span><span style="margin-left:-0.44em;">«</span>прятать» в словах, предложениях или картинках.<br />
 Будьте внимательны при работе с письмами! <br/><br/>
 Фразы вроде<span style="margin-right:0.44em;"> </span><span style="margin-left:-0.44em;">«</span>вставьте тут свою партнерскую ссылку» нужно заменять на свою ссылку.<br />
 Партнерские ссылки находятся в Личном кабинете партнера.  <br/><a href="https://docs.google.com/document/d/1O1RzVU3rsWbtQqYyvAZxk8toLwj_2EBgsBWD4UnfXYc/edit">Инструкция по входу в Личный кабинет партнера.</a></p>
    <div class="mat">
      <div v-for="(m,i) in materials" :key="i" class="m">
        <div class="title">{{m.name}}</div>
        <div>{{m.type}}</div>
        <a :style="m.link == 'nolink' ? 'color: rgba(0,0,0,0)': ''" href="#">{{m.link}}</a>
        <img :src="require('../assets/img/arrow-d.png')" />
        <div class="bot">
          <a >Баннеры →</a>
          <a>Посты для соцсетей →</a>
          <a>Письма для рассылки →</a>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  data(){
    return {
      materials: [
        {
          name: "Финансовая перезагрузка",
          type: "онлайн-курс",
          link: "http://fin-1.com/finrestart"
        },
        {
          name: "Клуб миллионеров в Москве",
          type: "тренинг",
          link: "nolink"
        },
        {
          name: "Матрица-Перезагрузка в Таиланде",
          type: "онлайн-курс",
          link: "http://www.vip-matrix.ru/"
        }
      ]
    }
  }
}
</script>
<style lang="stylus" scoped>
.sec8
  padding-bottom 40px
  .mat
    display flex
    width 80%
    margin auto
    justify-content space-around
    margin-top 40px
  .m
    width 240px
    padding 20px
    //height 240px
    border 1px solid lightgray
    border-radius 5px
    text-align center
    img
      width 7px
      margin-top 5px
      margin-bottom 5px
      display block
      margin-left calc(50% - 3px)
    .bot
      width 90%
      margin auto
      margin-top 20px
      a
        display block
        text-align left
        white-space nowrap
    .title
      font-family 'GothamProBold'
      margin-top 20px
  h2
    font-family 'GothamProBold'
    text-align center
    margin auto
    margin-top 40px
    width 90%
  p
    font-family 'Open Sans'
    width 90%
    margin auto
    max-width 420px
    margin-top 40px
@media  (max-width: 880px)
  .sec8
    .mat
      flex-direction column
    .m
      width 100%
      margin-top 40px

</style>



